html,body
{
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    overflow: hidden;
    /* cursor: none; */
}

/* Transition settings */
.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 1200ms ease-in-out;
  -o-transition: opacity 1200ms ease-in-out;
  transition: opacity 1200ms ease-in-out;
}

.page
{
  position: absolute;
  top: 0;
  background-color: rgb(13,7,8);
  width: 100%;
  height: 100vh;
}

.page a:link, .page a:visited
{
  background-color: rgba(100,7,40,0);
  color: rgb(251,248,249);
  text-align: center;
  text-decoration: none;
  display: inline-block;
  border-radius: 50%;
  /* cursor: none; */
  width: 100%;
  height: 100%;
}

.container
{
  background-image: url('button.png');
  width: 500px;
  height: 500px;
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
}

.page a:active
{
  background-color: rgba(134,59,78,0.2);
  cursor: none;
}

.container
{
  position: fixed;
  top: 50%;
  left: 50%;
  /* Find below using element inspect */
  margin-top: -250px;
  margin-left: -250px;
}


.videoContainer
{
  position: relative;
}

.videoContainer .ticker
{
  position: fixed;
  top: 10px;
  left: 10px;
  color: rgb(251,230,225);
  font-size: 16pt;
  font-family: 'Roboto', sans-serif;
  margin: none;
}

.recorder, .countdown
{
  height: 1200px;
  width: 1200px;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-top: -600px;
  margin-left: -600px;
  border-radius: 10%;
}

.recorder
{
  opacity: 0;
  -webkit-transition: opacity 1500ms ease-in;
  -o-transition: opacity 1500ms ease-in;
  transition: opacity 1500ms ease-in;
}

.fade-in
{
  opacity: 1;
}

.countdown
{
  opacity: 1;
  -webkit-transition: opacity 4s ease-in;
  -o-transition: opacity 4s ease-in;
  transition: opacity 4s ease-in;
}

.fade-out
{
  opacity: 0;
}



